@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Quicksand');

:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: "Quicksand", Arial;
  color: var(--secondary);
  margin: 0;
}

.App {
  margin: 0 auto;
}

.title h1 {
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}

.title h2,
.title p {
  text-align: center;
}

.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
form {
  margin: 30px auto 10px;
  text-align: center;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

label {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}

label:hover {
  background: var(--primary);
  color: white;
}

.output {
  height: 60px;
  font-size: 0.8rem;
}

.error {
  color: var(--error);
}

/* image grid styles */
.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}

@media screen and (max-width: 767px) {
  .img-grid {
    margin: 20px auto;
    display: block;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
  }

  .img-wrap {
    border-radius: 15px;
    max-width: 90%;
    position: absolute;
    top: 0;
    left: 18px;
  }

  .fontTag {
    padding: 2px !important;
    font-size: 14px !important;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 768px) {
  .titleText {
    margin-top: 90px !important;
  }
}

.img-wrap {
  overflow: hidden;
  height: 40px;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}

.img-wrap img {
  border-radius: 15px;
  min-width: 100%;
  max-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.searchBar {
  position: relative;
  font-size: 32px;
  width: 90%;
  left: 1.5%;
  padding-left: 15px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
  line-height: 50px;
}

.imgTag {
  position: relative;
  font-size: 38px;
  padding: 10px;
  width: 90%;
  left: 2.5%;
  height: 45px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
}

.filterContainer {
  flex-direction: row;
  display: flex;
  justify-content: flex-start;
  padding: 10px
}

.fontTag {
  user-select: none;
  cursor: pointer;
  color: #8dae10;
  border: 1px solid #8dae10;
  border-radius: 15px;
  background-color: white;
  padding: 5px;
  margin: 0 2px
}

/* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  border-radius: 25px;
}

.backdrop text {
  font-weight: bold;
  font-size: 32px;
  color: red;
  display: block;
  margin: 60px auto;
  max-height: 80%;
  max-width: 60%;
}

.titleText {
  user-select: none;
  cursor: pointer;
  text-transform: capitalize;
  font-weight: bold;
  margin-top: 110px;
  text-align: left;
  background-image: linear-gradient(to left, #86a901, #3aaed8);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  color: transparent;
}

.wrapper {
  position: relative;
  font-size: 32px;
  padding: 10px;
  width: 90%;
  left: 2.5%;
  height: 45px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  padding: 4px;
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 2px;
  transform: translateY(-50%);
}

.myImgTags {
  margin: 0 2px;
  padding: 5px;
  font-size: 12px;
  user-select: none;
  cursor: pointer;
  color: #8dae10;
  border: 1px solid #8dae10;
  border-radius: 15px;
  background-color: white;
}

*:focus {
  outline: none;
}