@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand);
:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: "Quicksand", Arial;
  color: #4e4e4e;
  color: var(--secondary);
  margin: 0;
}

.App {
  margin: 0 auto;
}

.title h1 {
  color: #efb6b2;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}

.title h2,
.title p {
  text-align: center;
}

.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
form {
  margin: 30px auto 10px;
  text-align: center;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

label {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #efb6b2;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: #efb6b2;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}

label:hover {
  background: #efb6b2;
  background: var(--primary);
  color: white;
}

.output {
  height: 60px;
  font-size: 0.8rem;
}

.error {
  color: #ff4a4a;
  color: var(--error);
}

/* image grid styles */
.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}

@media screen and (max-width: 767px) {
  .img-grid {
    margin: 20px auto;
    display: block;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
  }

  .img-wrap {
    border-radius: 15px;
    max-width: 90%;
    position: absolute;
    top: 0;
    left: 18px;
  }

  .fontTag {
    padding: 2px !important;
    font-size: 14px !important;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 768px) {
  .titleText {
    margin-top: 90px !important;
  }
}

.img-wrap {
  overflow: hidden;
  height: 40px;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}

.img-wrap img {
  border-radius: 15px;
  min-width: 100%;
  max-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.searchBar {
  position: relative;
  font-size: 32px;
  width: 90%;
  left: 1.5%;
  padding-left: 15px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
  line-height: 50px;
}

.imgTag {
  position: relative;
  font-size: 38px;
  padding: 10px;
  width: 90%;
  left: 2.5%;
  height: 45px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
}

.filterContainer {
  flex-direction: row;
  display: flex;
  justify-content: flex-start;
  padding: 10px
}

.fontTag {
  -webkit-user-select: none;
          user-select: none;
  cursor: pointer;
  color: #8dae10;
  border: 1px solid #8dae10;
  border-radius: 15px;
  background-color: white;
  padding: 5px;
  margin: 0 2px
}

/* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  border-radius: 25px;
}

.backdrop text {
  font-weight: bold;
  font-size: 32px;
  color: red;
  display: block;
  margin: 60px auto;
  max-height: 80%;
  max-width: 60%;
}

.titleText {
  -webkit-user-select: none;
          user-select: none;
  cursor: pointer;
  text-transform: capitalize;
  font-weight: bold;
  margin-top: 110px;
  text-align: left;
  background-image: linear-gradient(to left, #86a901, #3aaed8);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  color: transparent;
}

.wrapper {
  position: relative;
  font-size: 32px;
  padding: 10px;
  width: 90%;
  left: 2.5%;
  height: 45px;
  border-radius: 30px;
  border: none;
  background-color: #F5F5F5;
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  padding: 4px;
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 2px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.myImgTags {
  margin: 0 2px;
  padding: 5px;
  font-size: 12px;
  -webkit-user-select: none;
          user-select: none;
  cursor: pointer;
  color: #8dae10;
  border: 1px solid #8dae10;
  border-radius: 15px;
  background-color: white;
}

*:focus {
  outline: none;
}
.navbar {
    background-color: #0a4e7b;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #60b4d4;
    width: 350px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  
  .nav-menu.active {
    left: -40px;
    transition: 350ms;
    top: -16px;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    cursor: pointer;
  }
  
  .nav-text div {
    text-decoration: none;
    color: black;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }

  .nav-text:nth-child(even) {
    background-color:  #a09c9c;
}
  
  #myId {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }

  .nav-text div:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #0a4e7b;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }

  .scrollbar
{
	margin-left: 22px;
	float: left;
	height: 300px;
	width: 65px;
	background: #F5F5F5;
	overflow-y: scroll;
	margin-bottom: 25px;
}

.force-overflow
{
	min-height: 450px;
}

#wrapper
{
	text-align: center;
	margin: auto;
}

#style-7::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
	border-radius: 10px;
}

#style-7::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

#style-7::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	background-image: -webkit-gradient(linear,
									   left bottom,
									   left top,
									   color-stop(0.44, rgb(122,153,217)),
									   color-stop(0.72, rgb(73,125,189)),
									   color-stop(0.86, rgb(28,58,148)));
}
